<template>
	<view class="list">
		<u-form 
			:model="form" 
			ref="uForm"
			label-position="top"
		>
			<u-form-item label="" prop="title" required>
				<u-input v-model="form.title" type="select" @click="handleActionSheetShow" />
				<u-action-sheet :list="actionSheetList" v-model="show" @click="actionSheetCallback"></u-action-sheet>
			</u-form-item>
			<u-form-item label="" prop="info" >
				<u-input v-model="form.info" type="textarea" maxlength="-1" />
			</u-form-item>
		</u-form>
		<u-button @click="submit" type="primary"></u-button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				cate: '',
				tid: '',
				show: false,
				actionSheetList: [
					{
						text: ''
					},
					{
						text: ''
					},
					{
						text: ''
					},
					{
						text: ''
					},
					{
						text: ''
					},
					{
						text: ''
					},
				],
				form: {
					title: '',
					info: ''
				},
				rules: {
					title: [
						{
							required: true,
							message: '',
							trigger: ['blur', 'change'],
						}
					],
					info: [
						{
							max: 200,
							message: '200',
							trigger: ['blur', 'change'],
						}
					]
				}
			}
		},
		onLoad(opt) {
			console.log(opt)
			this.cate = opt.cate
			this.tid = opt.tid
			this.uid = opt.uid
			this.reportInfo = opt.reportInfo
			
		},
		onReady() {
			this.$refs.uForm.setRules(this.rules);
		},
		methods: {
			handleActionSheetShow() {
				this.show = !this.show
			},
			actionSheetCallback(index) {
				this.form.title = this.actionSheetList[index].text
			},
			submit() {
				this.$refs.uForm.validate(valid => {
					if (valid) {
						console.log('');
						this.handleAddTipOff()
					} else {
						console.log('');
					}
				});
			},
			async handleAddTipOff() {
				let t1 = this.uid ? `${this.uid}`: ''
				let t2 = this.uid ? `${this.reportInfo}...` : ''
				let t3 = `${this.form.title}`
				let i = `${this.form.info}`
				let res = await this.$https.post('/Home/Jzbxcx/add_tip_off', {
						id: this.tid,
						cate: this.cate,
						info: t1 + t2 + t3 + i						
					})
				if(res.data.code == 1) {
					uni.showToast({
						title: '',
						icon: 'none',
						duration: 1500
					})
					setTimeout(() => {
						uni.navigateBack({
							delta: 1
						})
					}, 1500)
					
				}else {					
					uni.showToast({
						title: '',
						icon: 'none',
						duration: 1500
					})
				}
			}
		}
	}
</script>

<style scoped lang="scss">
	.list {
		background-color: #fff;
		padding: 0 30rpx 80rpx;
	}
</style>
